import React from 'react'
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from 'react-vertical-timeline-component'
import 'react-vertical-timeline-component/style.min.css'

const ExperienceScreen = () => {
  return (
    <VerticalTimeline>
      <VerticalTimelineElement
        className='vertical-timeline-element--work'
        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
        date='2020 - PRESENT'
        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
      >
        <h3 className='vertical-timeline-element-title'>Lecturer 1</h3>
        <h4 className='vertical-timeline-element-subtitle'>
          University of the Philippines Manila
        </h4>
        <p>Database Systems, Interactive Computer Graphics</p>
      </VerticalTimelineElement>
      <VerticalTimelineElement
        className='vertical-timeline-element--work'
        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
        date='2018 - PRESENT'
        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
      >
        <h3 className='vertical-timeline-element-title'>Backend Developer</h3>
        <h4 className='vertical-timeline-element-subtitle'>PLDT GLOBAL</h4>
        <p>PAAS, Vortex, Smat Global Load, SW247 </p>
      </VerticalTimelineElement>
      <VerticalTimelineElement
        className='vertical-timeline-element--work'
        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
        date='2017'
        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
      >
        <h3 className='vertical-timeline-element-title'>Data Science Intern</h3>
        <h4 className='vertical-timeline-element-subtitle'>PLDT GLOBAL</h4>
      </VerticalTimelineElement>
    </VerticalTimeline>
  )
}

export default ExperienceScreen
