import React, { useState } from 'react'
import { Card, Badge } from 'react-bootstrap'
import ReactCardFlip from 'react-card-flip'
const Project = ({ project }) => {
  const listItems = project.technologies.map((technology) => (
    <Badge className='m-1' variant='dark' style={{ fontSize: '15px' }}>
      {technology}
    </Badge>
  ))

  const [isFlipped, setIsFlipped] = useState(false)
  const handleClick = () => {
    setIsFlipped(!isFlipped)
  }
  return (
    // <Card className='my-3 p-3 rounded'>
    //   <a href={`/project/${project._id}`}>
    //     <Card.Img src={project.image} variant='top' />
    //   </a>

    //   <Card.Body>
    //     <a href={`/project/${project._id}`}>
    //       <Card.Title as='div'>
    //         <strong>{project.name}</strong>
    //       </Card.Title>
    //     </a>

    //     <Card.Text as='h3'>${project.price}</Card.Text>
    //   </Card.Body>
    // </Card>
    // <Card className='my-3 rounded shadow-lg'>
    //   <Card.Img src={project.image} variant='top' />
    //   <Card.Body>
    //     <Card.Title className='d-flex justify-content-center'>
    //       {project.name}
    //     </Card.Title>
    //     <hr />
    //     <Card.Text>{project.description}</Card.Text>
    //   </Card.Body>
    //   <Card.Footer>
    //     <small className='text-muted'>{listItems}</small>
    //   </Card.Footer>
    // </Card>

    <ReactCardFlip isFlipped={isFlipped} flipDirection='vertical'>
      <Card onClick={handleClick} className='text-white mb-3'>
        <Card.Img src={'/rjbc-portfolio/' + project.image} variant='top' />
      </Card>

      <Card onClick={handleClick} className='m-3 p-3 rounded shadow-lg'>
        <Card.Body>
          <Card.Title className='d-flex justify-content-center'>
            {project.name}
          </Card.Title>
          <hr />
          <Card.Text>{project.description}</Card.Text>
        </Card.Body>
        <Card.Footer>
          <small className='text-muted'>{listItems}</small>
        </Card.Footer>
      </Card>
    </ReactCardFlip>
  )
}

export default Project
